<template>
    <div class="flex-user">
      <div
        style="
          background-color: #fff;
          margin-bottom: 10px;
          padding: 12px 24px;
          box-sizing: border-box;
          border-radius: 4px;
        "
      >
        <a-form :form="form" layout="vertical">
          <a-row :gutter="24">
            <a-col :span="4">
              <a-form-item label="操作用户">
                <a-input
                  placeholder="请输入用户名"
                  v-model="form.userName"
                ></a-input>
              </a-form-item>
            </a-col>
            <a-col :span="4">
              <a-form-item label="操作类型">
                <a-select
                  v-model="form.operationType"
                  placeholder="请选择操作类型"
                >
                  <a-select-option value="add">添加</a-select-option>
                  <a-select-option value="delete">删除</a-select-option>
                  <a-select-option value="update">修改</a-select-option>
                  <a-select-option value="query">查询</a-select-option>
                </a-select>
              </a-form-item>
            </a-col>
            <a-col :span="6">
              <a-form-item label="操作时间">
                <a-range-picker
                  v-model="form.operationTime"
                  style="width: 100%"
                  show-time
                  format="YYYY-MM-DD HH:mm:ss"
                />
              </a-form-item>
            </a-col>
            <a-col :span="4">
              <a-form-item label="操作对象">
                <a-input
                  placeholder="请输入操作对象"
                  v-model="form.operationObject"
                ></a-input>
              </a-form-item>
            </a-col>
            <a-col :span="6">
              <a-form-item :label="'\u00a0'">
                <g-btn text="查询" class="mr-2" type="primary" @callback='search'></g-btn>
                <g-btn text="重置" @callback='reset'></g-btn>
              </a-form-item>
            </a-col>
          </a-row>
        </a-form>
      </div>
  
      <div
        style="
          background-color: #fff;
          padding: 12px 24px 0;
          box-sizing: border-box;
          overflow-y: hidden;
          border-radius: 4px;
        "
      >
        <g-table-bar
          :spacing.sync="tableSize"
          :columns.sync="columns"
          :virtualColumns="virtualColumns"
          @refreshTable="refresh"
        >
          <g-btn text="导出 Excel" style="margin-right: 10px"></g-btn>
        </g-table-bar>
        <a-table
          id="tables"
          :columns="columns"
          :data-source="dataList"
          :scroll="{ y: tableheight }"
          :row-key="(record) => record.id"
          :pagination="pagination"
          @change="tableChange"
          :loading="isTableLoading"
          :size="tableSize"
        >
          <!-- 操作详情 -->
          <span slot="details" slot-scope="text">
            {{ text ? text.substring(0, 20) + '...' : '' }}
          </span>
          <!-- 操作 -->
          <span slot="action" slot-scope="text, record">
            <a href="javascript:;" @click="showDetails(record)">详情</a>
          </span>
        </a-table>
      </div>
    </div>
  </template>
  
  <script>
  import { getTableScroll } from "@/utils/getTableScroll";
  import { tableBase } from "@/utils";
  import moment from 'moment';
  import createModal from "@/utils/modal";
  
  export default {
    name: "equipment-audit-log",
    mixins: [tableBase],
    data() {
      return {
        tableheight: 0,
        form: {
          userName: "",
          operationType: undefined,
          operationTime: [],
          operationObject: ""
        },
        columns: [
          {
            title: "序号",
            dataIndex: "id",
            key: "id",
            customRender: (text, record, index) => `${index + 1}`,
            width: 80,
          },
          {
            title: "操作时间",
            dataIndex: "operationTime",
            key: "operationTime",
            sorter: true,
            width: 200,
            customRender: (text) => text ? moment(text).format('YYYY-MM-DD HH:mm:ss') : ''
          },
          {
            title: "操作用户",
            dataIndex: "userName",
            key: "userName",
          },
          {
            title: "操作类型",
            dataIndex: "operationType",
            key: "operationType",
          },
          {
            title: "操作对象",
            dataIndex: "operationObject",
            key: "operationObject",
          },
          {
            title: "操作详情",
            dataIndex: "details",
            key: "details",
            scopedSlots: { customRender: "details" },
            ellipsis: true
          },
          {
            title: "操作结果",
            dataIndex: "result",
            key: "result",
          },
          {
            title: "IP 地址",
            dataIndex: "ipAddress",
            key: "ipAddress",
          },
          {
            title: "操作",
            dataIndex: "action",
            key: "action",
            scopedSlots: { customRender: "action" },
            width: 100
          },
        ],
        virtualColumns: [],
        dataList: [] // 添加 dataList 用于存储数据
      };
    },
  
    mounted() {
        this.virtualColumns = JSON.parse(JSON.stringify(this.columns)).map(
            (item) => {
                item.checked = true;
                return item;
            }
        )
        this.$nextTick(() => {
            this.tableheight = getTableScroll({ id: "tables" });
        });
        // 模拟数据
        this.fetchData({
            pageSize: 10,
            pageNo: 1,
            successCallback: (res) => {
                this.dataList = res.items;
                this.pagination.total = res.totalCount;
            }
        });
    },
  
    methods: {
        // 查询
        search() {
            this.refresh();
        },
  
        // 重置
        reset() {
            this.form = {
                userName: "",
                operationType: undefined,
                operationTime: [],
                operationObject: ""
            };
            this.refresh();
        },
  
        // 获取数据
        fetchData(request) {
            this.isTableLoading = true;
            // 在这里模拟从后端获取数据
            setTimeout(() => {
                const data = [];
                for (let i = 0; i < request.pageSize; i++) {
                    data.push({
                        id: (request.pageNo - 1) * request.pageSize + i + 1,
                        operationTime: moment().subtract(i, 'minutes'),
                        userName: 'admin' + i,
                        operationType: ['添加', '删除', '修改', '查询'][Math.floor(Math.random() * 4)],
                        operationObject: '设备' + i,
                        details: '这是操作详情的详细信息，可能会很长，需要进行截断处理。' + i,
                        result: Math.random() > 0.5 ? '成功' : '失败',
                        ipAddress: '192.168.1.' + i
                    });
                }
                this.isTableLoading = false;
                request.successCallback && request.successCallback({
                    items: data,
                    totalCount: 100 // 假设总共有100条数据
                });
            }, 500);
        },
  
        // 显示详情
        showDetails(record) {
            // 使用 createModal 创建一个弹窗来显示详情
            createModal({
                title: '操作详情',
                width: '600px',
            }, {
                component: AuditLogDetail, // 使用一个单独的组件来显示详情
                data: record // 将当前记录作为数据传递给详情组件
            });
        },
        tableChange(pagination, filters, sorter) {
            this.pagination = pagination
            this.fetchData({
                pageSize: pagination.pageSize,
                pageNo: pagination.current,
                sortField: sorter.field,
                sortOrder: sorter.order,
                ...this.form,
                successCallback: (res) => {
                    this.dataList = res.items;
                    this.pagination.total = res.totalCount;
                }
            });
        }
    },
  };
  </script>
  
  <style lang="scss" scoped>
  .flex-user {
    box-sizing: border-box;
  }
  </style>